import _ from 'lodash';
import moment from 'moment';
import React from 'react';
import { useDispatch } from 'react-redux';

import { updateModel } from '@alliance-software/djrad/actions';
import useListModel from '@alliance-software/djrad/hooks/useListModel';

import requirePermissions from '../../../common/auth/hoc/requirePermissions';
import ScbpModelForm from '../../../common/data/ScbpModelForm';
import PageHeader from '../../../common/layout/PageHeader';
import { HourRateType } from '../models';

import styles from './HourRateTypeView.less';

function HourRateTypeView() {
    const dispatch = useDispatch();
    const { records, extraData, isLoading } = useListModel(HourRateType, {}, { refetchOn: false });
    if (isLoading) return null;
    const recordTable = {};
    records.toArray().forEach(record => {
        if (!recordTable[record.dayType]) {
            recordTable[record.dayType] = new Array(24);
        }
        recordTable[record.dayType][record.hour] = record;
    });
    const updateRecord = async (dayType, hour, newHourType) => {
        const record = recordTable[dayType][hour];
        dispatch(updateModel(record, { hourType: newHourType }));
    };

    return (
        <React.Fragment>
            <PageHeader htmlTitle="Peak Settings" header="Peak Settings" />
            <table>
                <tbody>
                    <tr>
                        <th />
                        {extraData.dayTypes.map(type => (
                            <th key={'header' + type}>{type[1]}</th>
                        ))}
                    </tr>
                    {_.range(24).map(hour => (
                        <tr key={hour}>
                            <th className={styles.hourLabel}>{moment({ hour }).format('h A')}</th>
                            {extraData.dayTypes.map(dayType => (
                                <td key={hour + '-' + dayType} className={styles.hourTypeField}>
                                    <ScbpModelForm
                                        record={recordTable[dayType[0]][hour]}
                                        footer={<></>}
                                        name={dayType[0] + '-' + hour}
                                        onSubmit={() => null}
                                    >
                                        <ScbpModelForm.Field
                                            name={'hourType'}
                                            allowClear={false}
                                            onChange={newValue => {
                                                updateRecord(dayType[0], hour, newValue);
                                            }}
                                        />
                                    </ScbpModelForm>
                                </td>
                            ))}
                        </tr>
                    ))}
                </tbody>
            </table>
        </React.Fragment>
    );
}

export default requirePermissions({ action: 'list', model: HourRateType })(HourRateTypeView);
