import PropTypes from 'prop-types';
import ButtonBar from '@alliance-software/djrad/components/form/ButtonBar';
import useModelFormProcessor from '@alliance-software/djrad/hooks/useModelFormProcessor';
import { modelInstance } from '@alliance-software/djrad/prop-types/model';
import React from 'react';
import ScbpModelForm from '../../../common/data/ScbpModelForm';

function PricingUpdateForm({ record, onSuccess }) {
    const { formProps } = useModelFormProcessor({
        record,
        onSuccess,
    });

    const footer = (
        <ButtonBar
            rightButtons={
                <ScbpModelForm.Button type="primary" htmlType="submit">
                    Save
                </ScbpModelForm.Button>
            }
        />
    );

    return (
        <ScbpModelForm
            fieldNames={[
                'hourlyInitialFee',
                'hourlyTier1StartAt',
                'hourlyTier2StartAt',
                'blockSizeMinutes',
                'hourlyTier1RatePerBlock',
                'hourlyTier2RatePerBlock',
                'outOfAreaBoundaryKm',
                'interstateFee',
                'airportSurcharge',
                'airportParkingFee',
                'governmentBookingFee',
                'companyBookingFee',
                'offPeakDiscountPercentage',
                'peakPercent',
                'peakMaxAmount',
                'outOfHoursFee',
                'publicHolidayOutOfHoursFee',
                'weddingRibbonFee',
                'childSeatFee',
                'additionalStopFee',
                'colorSelectionFee',
                'carParkPassFee',
            ]}
            footer={footer}
            {...formProps}
        />
    );
}

PricingUpdateForm.propTypes = {
    record: modelInstance(),
    onSuccess: PropTypes.func.isRequired,
};

export default PricingUpdateForm;
