import PropTypes from 'prop-types';
import React, { useState } from 'react';
import { Modal, Button } from 'antd';

import StripeForm from '../../../common/form/StripeForm';

export default function AssignCreditCardModal({ setStripeToken, buttonText }) {
    const [showModal, setShowModal] = useState(false);

    return (
        <>
            <Button onClick={() => setShowModal(true)}>{buttonText}</Button>

            <Modal
                title="Assign Credit Card"
                visible={showModal}
                footer={null}
                onCancel={() => setShowModal(false)}
            >
                <StripeForm onCreateToken={setStripeToken} showModal={setShowModal} />
            </Modal>
        </>
    );
}

AssignCreditCardModal.propTypes = {
    setStripeToken: PropTypes.func.isRequired,
    buttonText: PropTypes.string.isRequired,
};
