import PropTypes from 'prop-types';
import DateWidget from '@alliance-software/djrad/components/form/widgets/DateWidget';
import ModelLookupWidget from '@alliance-software/djrad/components/form/widgets/ModelLookupWidget';
import { Col, Row } from 'antd';
import React from 'react';
import ScbpModelForm from '../../../common/data/ScbpModelForm';
import DateTimeSplitWidget from '../../../common/form/DateTimeSplitWidget';
import TimeWidget from '../../../common/form/TimeWidget';
import BookingPanel from './BookingEntryPanel';

import styles from './BookingDetailsEntryPanel.less';
import BookingValueWidget from './BookingValueWidget';

const { Field, Item } = ScbpModelForm;

function PassengerLookupWidget({ items, ...props }) {
    const itemsWithOther = [...items, { id: 'other', label: 'Other' }];
    return <ModelLookupWidget.SelectWidget {...props} items={itemsWithOther} />;
}

PassengerLookupWidget.propTypes = {
    items: PropTypes.array.isRequired,
};

export default function BookingDetailsEntryPanel({ passenger }) {
    return (
        <BookingPanel label="Details">
            <Row gutter={30}>
                <Col span={8}>
                    <Item
                        name="passenger"
                        fieldProps={{
                            showAdd: false,
                            showUpdate: false,
                            lookupWidget: PassengerLookupWidget,
                        }}
                    />
                </Col>

                {passenger === 'other' && (
                    <>
                        <Col span={8}>
                            <Item label="Name" name="passengerName" />
                        </Col>
                        <Col span={8}>
                            <Item label="Contact number" name="passengerPhone" />
                        </Col>
                    </>
                )}
            </Row>
            <Row gutter={30}>
                <Col span={9}>
                    <Item>
                        <Field
                            name="travelOn"
                            widget={DateTimeSplitWidget}
                            renderWidgets={({
                                onDateChange,
                                dateValue,
                                onTimeChange,
                                timeValue,
                            }) => (
                                <div className={styles.travelOnWrapper}>
                                    <div>
                                        <div className="ant-col ant-form-item-label">
                                            <label htmlFor="travelOnDate">Travel Date:</label>
                                        </div>
                                        <BookingValueWidget
                                            // Name here is only used in BookingReadOnlyWidget
                                            // to determine if this should be read only
                                            name="travelOn"
                                            widget={DateWidget}
                                            onChange={onDateChange}
                                            onBlur={() => {}}
                                            value={dateValue}
                                        />{' '}
                                    </div>
                                    <div>
                                        <div className="ant-col ant-form-item-label">
                                            <label htmlFor="travelOnTime">Pick Up Time:</label>
                                        </div>
                                        <BookingValueWidget
                                            // Name here is only used in BookingReadOnlyWidget
                                            // to determine if this should be read only
                                            name="travelOn"
                                            widget={TimeWidget}
                                            minuteStep={5}
                                            use12Hours
                                            format="hh:mm a"
                                            onChange={onTimeChange}
                                            value={timeValue}
                                        />
                                    </div>
                                </div>
                            )}
                        />
                    </Item>
                </Col>
                <Col span={3}>
                    <Item name="isTimeTba" label="TBA" />
                </Col>
                <Col span={3} offset={1}>
                    <Item name="passengerCount" label="No. PAX" />
                </Col>
                <Col span={3}>
                    <Item label="Child < 8" name="childUnder8Count" />
                </Col>
                <Col span={3}>
                    <Item name="baggageCount" label="Baggage" />
                </Col>
            </Row>
        </BookingPanel>
    );
}

BookingDetailsEntryPanel.propTypes = {
    passenger: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
};
