import ActionLink from '@alliance-software/djrad/components/model/ActionLink';
import useGetModel from '@alliance-software/djrad/hooks/useGetModel';
import React from 'react';
import { PaymentMethodType } from '../../../choiceConstants';
import { numeric } from '../../../common/prop-types';
import { Account } from '../../../common/user/models';

export default function ExpiredCreditCardWarning({ accountId }) {
    const { record, isLoading, error } = useGetModel(Account, accountId, {
        partialRecordFieldNames: ['creditCardExpiryYear', 'creditCardExpiryMonth', 'paymentMethod'],
    });
    if (isLoading || error) {
        // Do nothing on error... not worth breaking entire page for user, worst case
        // is they don't get a warning they should
        return null;
    }
    if (record.paymentMethod !== PaymentMethodType.CREDIT_CARD.value) {
        return null;
    }
    const { creditCardExpiryYear: year, creditCardExpiryMonth: month } = record;
    const now = new Date();
    // Note that getMonth() return is 0 based
    if (now.getFullYear() > year || (now.getFullYear() === year && now.getMonth() + 1 > month)) {
        return (
            <div style={{ color: 'red' }}>
                Credit card expired.{' '}
                <ActionLink record={record} action="update">
                    Update
                </ActionLink>{' '}
                before finalising booking.
            </div>
        );
    }
    return null;
}

ExpiredCreditCardWarning.propTypes = {
    /**
     * The ID of the account to check expiry for. If expired will display a warning, otherwise won't render anything.
     */
    accountId: numeric.isRequired,
};
