import React, { useCallback, useContext, useLayoutEffect, useMemo, useRef, useState } from 'react';

export const DEFAULT_LAYOUT = 'default';
export const MINIMAL_LAYOUT = 'minimal';
const validLayouts = [DEFAULT_LAYOUT, MINIMAL_LAYOUT];
const themeContext = React.createContext();

export function ThemeProvider({ children }) {
    const [layout, setLayout] = useState(DEFAULT_LAYOUT);
    const setThemeLayout = useCallback(
        value => {
            if (value === layout) {
                return;
            }
            if (!validLayouts.includes(value)) {
                throw new Error(
                    `Invalid layout value ${value}. Must be one of ${validLayouts.join(', ')}`
                );
            }
            setLayout(value);
        },
        [layout]
    );
    const contextValue = useMemo(
        () => ({
            layout,
            setThemeLayout,
        }),
        [layout, setThemeLayout]
    );
    return (
        <themeContext.Provider value={contextValue}>{children(contextValue)}</themeContext.Provider>
    );
}

export function useThemeLayout(layout) {
    const { layout: currentLayout, setThemeLayout } = useContext(themeContext);
    const originalLayout = useRef(currentLayout);
    useLayoutEffect(() => {
        const originalLayoutValue = originalLayout.current;
        setThemeLayout(layout);
        return () => setThemeLayout(originalLayoutValue);
    }, [currentLayout, layout, setThemeLayout]);
}
