import PropTypes from 'prop-types';
import React, { useState } from 'react';
import { Button, message } from 'antd';
import useSettings from '@alliance-software/djrad/hooks/useSettings';
import { StripeProvider, Elements, CardElement, injectStripe } from 'react-stripe-elements';

function _StripeAddCard(props) {
    const [loading, setLoading] = useState(false);

    const handleSubmit = ev => {
        ev.preventDefault();
        ev.stopPropagation();
        setLoading(true);
        props.stripe.createToken().then(payload => {
            setLoading(false);
            if (payload.error) {
                message.error(payload.error.message);
                return;
            }
            props.showModal(false);
            props.onCreateToken(payload);
        });
    };
    return (
        <form onSubmit={handleSubmit}>
            <CardElement />
            <Button loading={loading} type="primary" htmlType="submit">
                Assign Card
            </Button>
        </form>
    );
}

_StripeAddCard.propTypes = {
    stripe: PropTypes.object.isRequired,
    onCreateToken: PropTypes.func.isRequired,
    showModal: PropTypes.func.isRequired,
};

const StripeAddCard = injectStripe(_StripeAddCard);

export default function StripeForm(props) {
    const { stripeApiKey } = useSettings();
    return (
        <StripeProvider apiKey={stripeApiKey}>
            <Elements>
                <StripeAddCard {...props} />
            </Elements>
        </StripeProvider>
    );
}
