import { BodyStyle } from 'alliance-react';
import { Empty, Icon, Skeleton } from 'antd';
import cx from 'classnames';
import { List } from 'immutable';
import moment from 'moment';
import PropTypes from 'prop-types';
import React, { useCallback, useEffect, useState } from 'react';
import { useDispatch } from 'react-redux';
import { AutoSizer, Column, defaultTableHeaderRenderer, Table } from 'react-virtualized';
import 'react-virtualized/styles.css';

import { updateModel } from '@alliance-software/djrad/actions';
import useFormActions from '@alliance-software/djrad/hooks/useFormActions';
import { modelClass } from '@alliance-software/djrad/prop-types/model';

import DispatchDetailPanel from '../components/DispatchDetailPanel';
import DispatchFilterForm from '../components/DispatchFilterForm';
import requirePermissions from '../../../common/auth/hoc/requirePermissions';
import { DispatchBooking } from '../models';
import ScbpModelForm from '../../../common/data/ScbpModelForm';
import { getColumnFieldNames } from '../../../common/data/util';
import useListView from '../../../common/hooks/useListView';
import PageHeader from '../../../common/layout/PageHeader';
import { MINIMAL_LAYOUT, useThemeLayout } from '../../theme';

import styles from '../dispatch.less';

function DispatchListFormItem({ value, record, fieldName }) {
    const dispatch = useDispatch();
    const [isEditing, setEditing] = useState(false);
    const onNonEditingClick = () => setEditing(true);
    const onSaveEdit = updatedRecord => {
        setEditing(false);
        const updatedFields = {};
        updatedFields[fieldName] = updatedRecord[fieldName];
        return dispatch(updateModel(record, updatedFields));
    };

    if (!isEditing) {
        return (
            <div className={styles.dispatchFormFieldToggle} onClick={onNonEditingClick}>
                <span className={styles.dispatchFormFieldValue}>{value}</span>
                <Icon type="edit" />
            </div>
        );
    }
    return (
        <ScbpModelForm
            name={`dispatchTable${record.id}${fieldName}`}
            record={record}
            footer={null}
            onSubmit={onSaveEdit}
        >
            <ScbpModelForm.Item name={fieldName} label={false}>
                <ScbpModelForm.Field name={fieldName} autoFocus />
            </ScbpModelForm.Item>
        </ScbpModelForm>
    );
}

DispatchListFormItem.propTypes = {
    value: PropTypes.any,
    record: PropTypes.object.isRequired,
    fieldName: PropTypes.string.isRequired,
};

function useRefreshTimer(onRefresh) {
    const [forced, setForce] = useState(false);
    useEffect(() => {
        const intervalId = setInterval(onRefresh, 120 * 1000);
        return () => clearInterval(intervalId);
    }, [onRefresh, forced]);
    return () => {
        onRefresh();
        setForce(isForced => !isForced);
    };
}

/**
 * Takes the sort order string returned by Djrad and converts it into sort options expected
 * by react-virtualized table
 * @param sortedField
 * @returns {{sortDirection: null, sortBy: null}|{sortDirection: string, sortBy: *}}
 */
function parseSortedField(sortedField) {
    if (!sortedField) {
        return { sortBy: null, sortDirection: null };
    } else if (sortedField.match(/^-.*$/)) {
        return { sortBy: sortedField.replace(/^-/, ''), sortDirection: 'DESC' };
    }
    return { sortBy: sortedField, sortDirection: 'ASC' };
}

function DispatchListTable({
    columns,
    data,
    isLoading,
    onChange,
    onRowClick,
    selectedRow,
    sortedField,
}) {
    // Set class names for table rows
    const rowClassName = ({ index }) => {
        if (index >= 0) {
            const record = data.get(index);
            const categoryClassName = styles[`${record.dispatchCategory}Row`];
            if (record.id === selectedRow) {
                return cx(categoryClassName, styles.selectedRow);
            }
            return categoryClassName;
        }
        return styles.dispatchListHeader;
    };

    // Sort configuration
    const { sortBy, sortDirection } = parseSortedField(sortedField);
    const sort = ({ sortBy: newSortBy, sortDirection: newSortDirection }) => {
        // We cycle through directions - ASC, then DESC, then null (default order)
        // Changing the field resets the cycle
        const directionMap = { ASC: 'ascend', DESC: 'descend' };
        const newSort = { field: null, order: null };
        if (sortBy !== newSortBy || sortDirection !== 'DESC') {
            newSort.field = newSortBy;
            newSort.order = directionMap[newSortDirection];
        }
        onChange(false, {}, newSort);
    };

    // Column render helper
    const headerRenderer = ({ columnData, ...props }) => {
        if (columnData.headerClassName) {
            return (
                <div className={cx(columnData.headerClassName)}>
                    {defaultTableHeaderRenderer(props)}
                </div>
            );
        }
        return defaultTableHeaderRenderer(props);
    };

    const renderedColumns = columns.map(column => {
        const { align, className, ...columnProps } = column;
        const rightAlignClassName = align === 'right' ? styles.alignRight : '';
        const columnData = {
            headerClassName: rightAlignClassName,
        };
        const trueClassName = cx(className, rightAlignClassName);
        return (
            <Column
                cellRenderer={({ cellData, rowData }) =>
                    column.render ? column.render(cellData, rowData) : cellData
                }
                className={trueClassName}
                columnData={columnData}
                headerRenderer={headerRenderer}
                key={columnProps.dataKey}
                {...columnProps}
            />
        );
    });

    // Row height
    // Default of 40 set to allow convoy and run number fields to display fully
    // Option length over 35 indicates it wraps to three lines which requires row height of 60
    const rowHeight = ({ index }) => (data.get(index).options.length > 35 ? 60 : 40);

    // Final render
    return (
        <AutoSizer>
            {({ height, width }) => (
                <Table
                    height={height}
                    headerHeight={30}
                    noRowsRenderer={() => (isLoading ? <Skeleton /> : <Empty />)}
                    onRowClick={({ rowData }) => onRowClick(rowData)}
                    rowClassName={rowClassName}
                    rowCount={isLoading ? 0 : data.size}
                    rowGetter={({ index }) => data.get(index)}
                    rowHeight={rowHeight}
                    sort={sort}
                    sortBy={sortBy}
                    sortDirection={sortDirection}
                    width={width}
                >
                    {renderedColumns}
                </Table>
            )}
        </AutoSizer>
    );
}

DispatchListTable.propTypes = {
    columns: PropTypes.arrayOf(PropTypes.object).isRequired,
    data: PropTypes.instanceOf(List).isRequired,
    isLoading: PropTypes.bool.isRequired,
    onChange: PropTypes.func.isRequired,
    onRowClick: PropTypes.func,
    selectedRow: PropTypes.number,
    sortedField: PropTypes.string,
};

function RawDispatchList({ model }) {
    /**
     * Use the same minimal admin layout as the booking screen - we need the real estate here
     */
    useThemeLayout(MINIMAL_LAYOUT);

    /**
     * Initial filter form handling
     */
    const filterFormName = 'DispatchFilterForm';
    const filterFormActions = useFormActions(filterFormName);

    /**
     * Dispatch list refresh countdown handlers
     */
    const refreshDispatchList = useCallback(() => {
        filterFormActions.submit();
    }, [filterFormActions]);
    const forceRefresh = useRefreshTimer(refreshDispatchList);

    /**
     * Column setup
     */
    const columns = [
        {
            align: 'right',
            dataKey: 'bookingNumber',
            label: 'Booking',
            render(value) {
                return <span className={styles.bookingNumber}>{value}</span>;
            },
            width: 90,
        },
        {
            dataKey: 'travelOn',
            label: 'Time',
            render(value) {
                return moment(value).format('HH:mm');
            },
            width: 60,
        },
        { dataKey: 'passengerName', label: 'Client', width: 180 },
        {
            dataKey: 'fromAddress',
            label: 'First Pickup',
            render(value) {
                return value.suburb;
            },
            width: 160,
        },
        {
            dataKey: 'destinationAddress',
            label: 'Final Destination',
            render(value, record) {
                let destination = 'As Directed';
                if (value) {
                    destination = value.suburb;
                }
                if (record.bookingAdditionalStops.size > 0) {
                    destination = `${destination} (+${record.bookingAdditionalStops.size + 1})`;
                }
                return destination;
            },
            width: 160,
        },
        {
            align: 'right',
            dataKey: 'bookingPriority',
            label: 'Prty',
            render(value) {
                if (value !== 3) {
                    return value;
                }
                return ' ';
            },
            width: 60,
        },
        {
            align: 'right',
            dataKey: 'passengerCount',
            label: 'Pax',
            width: 60,
        },
        {
            align: 'right',
            dataKey: 'convoyNumber',
            label: 'Conv',
            className: cx(styles.dispatchFormField, styles.dispatchConvoyColumn),
            render(value, record) {
                return (
                    <DispatchListFormItem value={value} fieldName="convoyNumber" record={record} />
                );
            },
            width: 90,
        },
        {
            dataKey: 'car',
            label: 'Car',
            width: 60,
        },
        {
            dataKey: 'options',
            label: 'Requests',
            width: 160,
            render(value) {
                return <div className={styles.optionColumn}>{value}</div>;
            },
        },
        {
            align: 'right',
            dataKey: 'runNumber',
            label: 'Run',
            className: styles.dispatchFormField,
            render(value, record) {
                return (
                    <DispatchListFormItem value={value} record={record} fieldName={'runNumber'} />
                );
            },
            width: 90,
        },
        { dataKey: 'driverNumber', label: 'Driver', width: 80 },
        { dataKey: 'displayBookingStatus', label: 'Status', width: 90 },
        { dataKey: 'pay', label: 'Pay', width: 100 },
    ];

    /**
     * Initial filter properties
     */
    const today = moment().format('YYYY-MM-DD');
    const partialRecordFieldNames = getColumnFieldNames(model, [
        ...columns.map(column => column.dataKey),
        'dispatchCategory',
        'bookingAdditionalStops',
    ]);
    const { extraData, filterProps, isInitialLoad, isLoading, records, tableProps } = useListView(
        model,
        partialRecordFieldNames,
        {
            initialState: {
                travelDate_after: today,
                travelDate_before: today,
                bookingStatus: ['Active'],
            },
            refetchOn: null,
        }
    );

    /**
     * Filter form
     */
    const renderFilter = thisFilterProps => (
        <DispatchFilterForm
            {...thisFilterProps}
            formName={filterFormName}
            refresh={forceRefresh}
            extraData={extraData}
        />
    );

    /**
     *  Row selection from table
     */
    const [selectedRecord, selectRecord] = useState(null);
    const onRow = record => selectRecord(record.id);
    useEffect(() => {
        const recordIds = records ? records.map(record => record.id) : [];
        const resetRecord = () => {
            if (records && records.first()) {
                selectRecord(records.first().id);
            } else {
                selectRecord(null);
            }
        };
        if (selectedRecord) {
            if (!recordIds.includes(selectedRecord)) {
                resetRecord();
            }
        }
        if (!selectedRecord && records && records.first()) {
            resetRecord();
        }
    }, [selectedRecord, records]);

    /**
     * Final render
     */
    return (
        <BodyStyle className="dispatch">
            <>
                <PageHeader htmlTitle="Dispatch" header="Dispatch" />
                <div className={styles.dispatchWrapper}>
                    <div className={styles.dispatchListColumn}>
                        {renderFilter && renderFilter(filterProps)}
                        {isInitialLoad && <Skeleton />}
                        {!isInitialLoad && (
                            <div className={styles.dispatchTableWrapper}>
                                <DispatchListTable
                                    {...tableProps}
                                    columns={columns}
                                    onRowClick={onRow}
                                    selectedRow={selectedRecord}
                                    isLoading={isLoading}
                                />
                            </div>
                        )}
                    </div>
                    <div className={styles.dispatchDetailColumn}>
                        {selectedRecord && <DispatchDetailPanel recordId={selectedRecord} />}
                    </div>
                </div>
            </>
        </BodyStyle>
    );
}

RawDispatchList.propTypes = {
    model: modelClass().isRequired,
};

export default requirePermissions({ action: 'list', model: DispatchBooking })(RawDispatchList);
