import { Record, Maybe, Union } from 'typed-immutable';

export const BookingPriceBreakdown = Record({
    // Maybe accounts for the absence of the base component for interstate bookings
    base: Maybe(
        Union(
            // One-way record
            Record({
                distance: Number,
                tier1: Number,
                tier2: Maybe(Number),
                tier3: Maybe(Number),
                carClass: Number,
                peak: Maybe(Number),
                subtotal: Number,
            }),
            // One-way special event record
            Record({
                distance: Number,
                eventMinimum: Number,
                subtotal: Number,
            }),
            // One-way price override record
            Record({
                distance: Number,
                special: Number,
                subtotal: Number,
            }),
            // Hourly record
            Record({
                time: String,
                firstHour: Number,
                tier1: Maybe(Number),
                tier2: Maybe(Number),
                carClass: Number,
                peak: Maybe(Number),
                subtotal: Number,
            })
        )
    ),
    fees: Union(
        Record({
            government: Number,
            company: Maybe(Number),
            airport: Maybe(Number),
            outOfArea: Maybe(Number),
            airportParking: Maybe(Number),
            event: Maybe(Number),
            subtotal: Number,
        }),
        Record({ interstate: Number, subtotal: Number })
    ),
    options: Record({
        ribbon: Number,
        childSeats: Number,
        color: Number,
        additionalStops: Number,
        carParkPass: Number,
        subtotal: Number,
    })({
        ribbon: 0,
        childSeats: 0,
        color: 0,
        additionalStops: 0,
        carParkPass: 0,
        subtotal: 0,
    }),
    total: Number,
    eventName: Maybe(String),
});
