import React from 'react';
import PropTypes from 'prop-types';
import moment from 'moment';
import { Button, Col, Row } from 'antd';
import CheckboxWidget from '@alliance-software/djrad/components/form/widgets/CheckboxWidget';
import DateRangeWidget from '@alliance-software/djrad/components/form/widgets/DateRangeWidget';
import useFormActions from '@alliance-software/djrad/hooks/useFormActions';
import useFormValues from '@alliance-software/djrad/hooks/useFormValues';
import { RangeReduxFormField } from '@alliance-software/djrad/model/fields/RangeField';

import { DispatchBooking } from '../models';
import ScbpModelFilterForm from '../../../common/data/ScbpModelFilterForm';

import styles from '../dispatch.less';

function DispatchHighlightKey() {
    return (
        <div className={styles.dispatchHighlightKey}>
            <legend>Key:</legend>
            <div>
                <div>
                    <span className={styles.interstate}>&nbsp;</span> Interstate
                </div>
                <div>
                    <span className={styles.tba}>&nbsp;</span> TBA
                </div>
                <div>
                    <span className={styles.outOfArea}>&nbsp;</span> Outside of Area
                </div>
                <div>
                    <span className={styles.hold}>&nbsp;</span> Hold
                </div>
                <div>
                    <span className={styles.request}>&nbsp;</span> Request
                </div>
            </div>
        </div>
    );
}

function DispatchFilterDateRangeWidget({ onRelativeDateChange, ...rest }) {
    return (
        <Row>
            <Col span={2}>
                <Button
                    className={styles.listFilterGridComponent}
                    icon="left"
                    onClick={onRelativeDateChange(-1)}
                />
            </Col>
            <Col span={20}>
                <DateRangeWidget className={styles.listFilterGridComponent} format="LL" {...rest} />
            </Col>
            <Col span={2}>
                <Button
                    className={styles.listFilterGridComponent}
                    icon="right"
                    onClick={onRelativeDateChange(1)}
                />
            </Col>
        </Row>
    );
}

DispatchFilterDateRangeWidget.propTypes = {
    /**
     * Required function definition:
     * onRelativeDateChange(number.isRequired) -> eventHandler
     * **/
    onRelativeDateChange: PropTypes.func.isRequired,
};

function DispatchFilterFormFooter({ refresh }) {
    const rightButtons = [
        <ScbpModelFilterForm.Button
            type="primary"
            ghost
            icon="sync"
            key="refresh"
            onClick={refresh}
        >
            Refresh data
        </ScbpModelFilterForm.Button>,
    ];
    return <div>{rightButtons}</div>;
}

DispatchFilterFormFooter.propTypes = {
    refresh: PropTypes.func.isRequired,
};

export default function DispatchFilterForm({ formName, refresh, extraData, ...props }) {
    const dateRangeFormat = 'YYYY-MM-DD';
    const formActions = useFormActions(formName);
    const fieldValues = useFormValues(formName, ['travelDate']);

    const onRelativeDateChange = numDays => () => {
        const newFieldValues = {
            lower: moment(fieldValues.lower)
                .add(numDays, 'days')
                .format(dateRangeFormat),
            upper: moment(fieldValues.upper)
                .add(numDays, 'days')
                .format(dateRangeFormat),
        };
        formActions.change('travelDate', newFieldValues);
    };

    return (
        <ScbpModelFilterForm
            name={formName}
            model={DispatchBooking}
            layout="inline"
            onChange={formActions.submit}
            footer={null}
            className={styles.filterForm}
            {...props}
        >
            <fieldset>
                <legend>Filters:</legend>

                <Row>
                    <Col span={12}>
                        <ScbpModelFilterForm.Item label="Date" colon={false} name="travelDate">
                            <ScbpModelFilterForm.Field
                                name="travelDate"
                                reduxFormFieldComponent={RangeReduxFormField}
                                widget={DispatchFilterDateRangeWidget}
                                widgetProps={{ onRelativeDateChange }}
                            />
                        </ScbpModelFilterForm.Item>
                    </Col>
                    <Col span={8}>
                        <ScbpModelFilterForm.Item
                            label="Status"
                            colon={false}
                            name="bookingStatus"
                            className={styles.bookingStatusField}
                            fieldProps={{
                                // Never disable this field. When form is submitting it would normally be disabled
                                // but because we submit on change it happens while the input is still open... which
                                // then breaks the select dropdown in some browsers (eg. in Chrome you can't then
                                // close the dropdown).
                                disabled: false,
                            }}
                        />
                    </Col>
                    <Col span={4}>
                        <ScbpModelFilterForm.Item>
                            <DispatchFilterFormFooter
                                submit={formActions.submit}
                                refresh={refresh}
                            />
                        </ScbpModelFilterForm.Item>
                    </Col>
                </Row>
                <Row>
                    <Col span={12}>
                        <DispatchHighlightKey />
                    </Col>
                    <Col span={2}>&nbsp;</Col>
                    <Col span={6}>
                        <ScbpModelFilterForm.Item
                            name="interstate"
                            label={false}
                            fullWidth
                            className={styles.interstateField}
                        >
                            <ScbpModelFilterForm.Field
                                name="interstate"
                                widget={CheckboxWidget}
                                widgetProps={{ label: 'Interstate Only?' }}
                            />
                            <label className={styles.listFilterCheckboxLabel} htmlFor="interstate">
                                Interstate Only?
                            </label>
                        </ScbpModelFilterForm.Item>
                    </Col>
                    {extraData && (
                        <Col className={styles.bookingCounts} span={3}>
                            {extraData.filteredBookingCount} of {extraData.rangeBookingCount}{' '}
                            bookings
                        </Col>
                    )}
                </Row>
            </fieldset>
        </ScbpModelFilterForm>
    );
}

DispatchFilterForm.propTypes = {
    formName: PropTypes.string.isRequired,
    refresh: PropTypes.func.isRequired,
    extraData: PropTypes.object,
};
