import PropTypes from 'prop-types';
import React from 'react';
import { Modal } from 'antd';
import useListModel from '@alliance-software/djrad/hooks/useListModel';

import { ReactComponent as IconPlane } from '../../../images/icon-plane.svg';
import { ReactComponent as IconHome } from '../../../images/icon-home.svg';
import { ReactComponent as IconOffice } from '../../../images/icon-businessman.svg';
import { ReactComponent as IconSaved } from '../../../images/icon-building.svg';
import IconButton from '../../../common/ui/IconButton';
import { numeric } from '../../../common/prop-types';
import { airportAddress } from '../../../consts';
import { ClientAddress } from '../models';
import { BookingAddressType } from '../../../choiceConstants';

import styles from './AddressPickerModal.less';

const iconsByType = {
    [BookingAddressType.HOME.value]: <IconHome />,
    [BookingAddressType.OFFICE.value]: <IconOffice />,
    [BookingAddressType.ADDRESS_BOOK.value]: <IconSaved />,
};

export default function AddressPickerModal({ visible, onAddressPicked, onClose, client }) {
    const { records, isLoading, error } = useListModel(
        ClientAddress,
        {
            client,
        },
        { refetchOn: false }
    );

    if (isLoading) {
        return null;
    }
    if (error) {
        throw error;
    }

    const pickAddress = address => {
        onAddressPicked(address);
        onClose();
    };

    return (
        <Modal
            title="Select Address to use"
            visible={visible}
            footer={null}
            onCancel={onClose}
            width={900}
        >
            <div className={styles.grid}>
                <div>
                    <IconButton
                        label="Airport"
                        onSelect={() => pickAddress(airportAddress)}
                        icon={<IconPlane />}
                        size="large"
                    />
                </div>
                {records &&
                    records.map(address => (
                        <div key={address.id}>
                            <IconButton
                                label={address.addressLabel}
                                onSelect={() => pickAddress(address)}
                                icon={iconsByType[address.addressType]}
                                size="large"
                            />
                        </div>
                    ))}
            </div>
        </Modal>
    );
}

AddressPickerModal.propTypes = {
    onClose: PropTypes.func.isRequired,
    visible: PropTypes.bool,
    onAddressPicked: PropTypes.func.isRequired,
    client: numeric.isRequired,
};
